.sidebar {
    display: none;

}

.sidebar.open {
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: 0 10px 40px 0 #1D212621;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 317px;
    height: 720px;
    background-color: #ffffff;
}

.background {
    background-color: #4180c3;
}

.nav {
    display: flex;
    flex-direction: column;
    font-size: 22px;
    line-height: 1.18;
    row-gap: 30px;
}

.nav a {
    display: flex;
    align-items: center;
    text-decoration: none;

}

.nav a:hover {
}

